.site-list {
  font-family: @Oswald;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  color: @black;
  text-transform: uppercase;
  list-style-type: none;
  margin: 0;
  padding: 0;
}

.site-list__item {
  padding: 22px 0 24px 0;
  border-top: 1px solid #e6e6e6;
  text-align: center;
}

.site-list__link {
  color: @black;
  text-decoration: none;
  font-size: 20px;
  font-weight: 500;
  line-height: 20px;
  text-transform: uppercase;

  &:hover {
    opacity: 0.6;
  }

  &:active {
    opacity: 0.3;
  }
}

@media (min-width: @tablet-width) {
  .site-list {
    width: 500px;
    display: flex;
    flex-wrap: wrap;
    justify-content: flex-end;
    gap: 0 35px;
  }

  .site-list__item {
    border: none;
    padding: 19px 0 8px;

    &--current {
      border-bottom: 2px solid @basic-green;
    }
  }

  .site-list__link {
    font-size: 18px;
    font-weight: 400;
    line-height: 24px;
    text-transform: uppercase;
  }
}

@media (min-width: @desktop-width) {
  .site-list {
    width: 800px;
  }

  .site-list__link {
    font-size: 20px;
    font-weight: 400;
    line-height: normal;
  }

  .site-list--index .site-list__link {
    color: @white;
  }

  .site-list--index .site-list__item--current {
    border-color: @white;
  }
}
